@media (min-width: 1200px) {
    .APP_PAGE {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr auto+10.85fr;
        grid-template-areas: "LOGO_PIC_AN_PAGE" "HEADER_AN_PAGE" "PROFILE" "MESSAGE" "SUBSCRIBERS";
        grid-gap: 1px;
        margin: 10px;
        padding: 2px 2px 2px 2px;
    }
}

@media (min-width: 1700px) {
    .APP_PAGE {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 0.5fr 0.5fr auto+10fr;
        grid-template-areas: "LOGO_PIC_AN_PAGE" "HEADER_AN_PAGE" "CONTENT_AN_PAGE";

        grid-gap: 1px;
        margin: 10px;
        padding: 2px 2px 2px 2px;
    }
}

/*________________________________________________________*/

.LOGO_PIC {
    grid-area: LOGO_PIC_AN_PAGE;
    display: flex;
    border: solid;
    justify-content: center;

}

.HEADER_AN_PAGE {
    grid-area: HEADER_AN_PAGE;
    display: flex;
    justify-content: space-around;
    text-align: center;
    align-items: baseline;
    height: auto;
    border: solid;
}

.CONTENT_AN_PAGE {
    display: grid;
    grid-template-areas: "PROFILE" "MESSAGE" "SUBSCRIBERS";

}

/*________________________________________________________*/
.PROFILE {
    grid-area: PROFILE;

    display: flex;
    padding: 5px;
    flex-flow: column;
}

.descript {
    display: flex;
    flex-flow: row;
    justify-content: space-around;
    border: solid;
}

.POST_FORM {
    border: solid;
    display: flex;
    flex-flow: row;
    height: 21px;
    margin: 1% 0 1% 0;

}

.POST {
    display: flex;
    flex-flow: column;

    display: flex;
    border: solid;
}

.POST_ITEM {
    background: aliceblue;
    margin-bottom: 1%;
    padding-left: 2%;
    flex-flow: row;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);

}

/*________________________________________________________*/
.MESSAGE {
    grid-area: MESSAGE;

    display: grid;
    grid-template-columns: 7fr 3fr;
    grid-template-rows: 9fr 1fr;

    grid-template-areas: "LIST_MESSAGE LIST_FRIENDS ";
"LIST_MESSAGE LIST_FRIENDS" grid-gap: 1 px;
    padding: 3px;
    height: 500px;
}

.MESSAGE_LIST {
    grid-area: LIST_MESSAGE;
    overflow: auto;
    padding: 3px;
    height: 500px;
    border: solid;
}

.MESSAGE_FORM {
    border: solid;
    display: flex;
    background: aqua;
}

.MESSAGE_ITEM {
    padding: -10px;
    border-radius: 1em;
    overflow: auto;
    word-wrap: break-word;
    max-width: 50%;

    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25),
    0 10px 10px rgba(0, 0, 0, 0.22);
}

.MESSAGE_ITEM, #USER {
    position: relative;
    left: 0;
    border-radius: 1em;
    margin: 3px 3px 3px 3px;
    padding: 1%;
    background-color: lightgreen;
}

.MESSAGE_ITEM, #PROFILE {
    position: relative;
    left: 50%;
    border-radius: 1em;
    margin: 3px 3px 3px 3px;
    padding: 1%;
    background-color: red;
}

.button_M {
    margin-top: 3px;
    height: 21px;
}

.pole_M {
    height: 15px;
    width: 100%;
    margin-left: 1px;
    margin-top: 3px;
    border-radius: 30px;
}

/*________________________________________________________*/
.FRIENDS_LIST {
    grid-area: LIST_FRIENDS;
    padding: 2px;
    border: solid;
}

.FRIEND_ITEM {
    display: flex;
    border-style: solid;
    border-radius: 1em;
    text-align: center;
    margin-top: 5%;
    padding-top: 2%;
}

.FRIEND_AVA {
    margin: 2px 2px 2px 2px;
    height: 40px;
    width: 40px;
    border-style: solid;
    border-radius: 20%;
    font-size: 6px;
}

/*________________________________________________________*/
.SUBSCRIBERS {
    grid-area: SUBSCRIBERS;
    border: solid;
    padding: 5px;
    display: flex;
    flex-flow: column;
    align-items: center;
}

.UserID {
    display: flex;
    background: azure;
    border-radius: 1em;
    text-align: center;
    min-width: 100px;
    padding: 0.2em;
    margin: 5px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.FuNam {
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    padding: 5px;
    width: 90%;
}

.FuNam div:nth-child(1) div:nth-child(1) {
    background: lightblue;
    text-align: justify;

}

.PicSub {
    display: flex;
    flex-flow: column;
    justify-content: center;
    margin: 2px 2px 2px 2px;
    height: 120px;
    width: 120px;

    border: solid forestgreen;
    border-radius: 20%;
    font-size: 6px;
}

.PicSub button, img {
    border-radius: 20%;
}

.PicSub button img {
    height: 100px;
    width: 100px;
    opacity: 1;
}

.PicSubi {
    display: flex;
    flex-flow: column;
    justify-content: center;
    margin: 2px 2px 2px 2px;
    height: 120px;
    width: 120px;

    border: solid red;
    border-radius: 20%;
    font-size: 6px;
}

.PicSubi button, img {
    border-radius: 20%;
}

.PicSubi button img {
    height: 100px;
    width: 100px;
    opacity: 1;
}

#ter {
    color: red;
}

.LOGIN {

}

.ListFriend {
    background: brown;
}